import React from 'react'
import Category from '../components/category'
import Header from '../components/header'
import Current from '../components/current'
import superagent from 'superagent'
import CardExpanded from '../components/CardExpanded.jsx'
import Loader from '../components/loader'
import ShowCard from '../components/showCard.js'
import Environment from '../components/environment.js'
import DatePicker from  'react-date-picker'
import Art from '../components/art.js'
import Report from '../components/reports.js'
import International from '../components/international.js'
import Defence from '../components/defence.js'
import National from '../components/nationalbodies.js'

export default class HomePage extends React.Component{
	constructor(props){
		super(props)
		this.state={
			latestList:[],
			category:"All Article's",
			open:false,
			popValue:[],
			loading:true,
			status:'',
			sectionList:[],
			constList:[],
			sectionConstList:[],
			showSection:"Today's Current",
			showCalender:'true',
			showContent:"Today's Current",
			sectionAvail:false
		}
		this.callback=this.callback.bind(this)
		this.changeCategory = this.changeCategory.bind(this)
		this.closeExpanded= this.closeExpanded.bind(this)
		this.arrayBufferToBase64=this.arrayBufferToBase64.bind(this)
		this.changeSection = this.changeSection.bind(this)
		this.toggelSection = this.toggelSection.bind(this)
		this.date=new Date()
	}
	closeExpanded(value){
		if(value===true)
		this.setState({
			open:false
		})
	}
	callback(value,gprops){
		console.log('inside homepage',value)
		console.log('gprops',gprops)
		this.setState({
			open:true,
			popValue:gprops
		})
	}
	componentDidMount(){
		this.fetchData()
	}
	fetchData(){
		superagent.get('/api/content')
		.then((res)=>{
			if(res.text==="error in fetching data"){
				this.setState({
					loading:false,
					status:'Error in Fetching Data'
				})
			}
			else{
				var data=JSON.parse(res.text)
				if(data.text.length == 0){
					this.setState({
						loading:false,
						status:'No Article Found',
						sectionAvail:true
					})
				} else {
					this.setState({
						allList:data,
						latestList:data
					})

					var base64Flag = 'data:image/jpeg;base64,';
					this.state.latestList.text.map((data)=>{
						this.img=data.photo
						if(typeof(this.img.data.data)==="object" ){
						var imageStr = this.arrayBufferToBase64(this.img.data.data);
				        this.finalImg=base64Flag+imageStr
				        data.photo=this.finalImg
				        this.setState({
				        	img:this.finalImg
				        })
				    }
					})
					this.setState({
						loading:false,
						constList:this.state.latestList
					})	
				
				this.changeSection("Today's Current")
				}
			}
		})
	}
	changeSection(value){
			let arr=[]
			let newList={}
			arr=this.state.constList.text.filter(data=>data.section==="Today's Current")
			newList.text=arr
			console.log(newList)
			if(newList.text.length===0)this.setState({status:'No Article Found',sectionAvail:true})
			else{
			this.setState({
				sectionList:newList,
				status:'got',
				sectionConstList:newList,
				sectionAvail:true
			})
		}
	}
	changeCategory(value){
		this.setState({
			category:value,
			loading:true
		})
		let newList={}
		if(value === "All Article's"){
			newList.text=this.state.sectionConstList.text
		}else{
		let oldList=this.state.sectionConstList.text.filter(data=> data.category===value)	
		newList.text=oldList
		}
		console.log('new',newList)
		this.setState({
		sectionList:newList,
		loading:false,
		status:'got'
		})
		if(newList.text.length===0)this.setState({status:'No Article Found'})
	}
	arrayBufferToBase64(buffer) {
    var binary = '';
    var bytes = [].slice.call(new Uint8Array(buffer));
    bytes.forEach((b) => binary += String.fromCharCode(b));
    return window.btoa(binary);
	}
	toggelSection(value){
		console.log('inside homepage closeSection',value)
		if(value!=="Today's Current"){
			this.setState({
				showContent:value
			})
		}
		if(value==="Today's Current"){
			this.setState({
				showContent:"Today's Current"
			})
		}
	}
	render(){
		return(
			<div className="back">
				<Header/>
				<br/><br/><br/><br/><br/>
				<div className="calender-div">
				<span className="text-section d-inline mt-5 ml-3">Today's Current</span>
				{this.state.showCalender ?
				<span className=" d-inline date-picker ml-3 mr-3"><DatePicker value={this.date}/></span> 
				 :<span></span>
				}
				</div>
				{this.state.showContent==="Today's Current" ?
				<div>
				<div>
				<Category category={this.changeCategory}/>
				<br/><br/>
				</div>
				<div className="show-content">
					<span className="ml-4 d-inline-flex">
					<span className="dot mt-2 mr-2"></span>
					<span className="text-category mb-5">{this.state.category}</span>
					</span>
					{this.state.loading ? <Loader/> : <span></span>}
					{this.state.status==='got' ? <ShowCard value={this.state.sectionList} open={this.callback}/> : <h2 className="mt-4 mb-5 pb-3 text-center">{this.state.status}</h2>}
				</div>
				</div>
				: <span></span>
				
				}
				{this.state.open ? <CardExpanded value={this.state.popValue} close={this.closeExpanded}/> : <span></span>}	
				{this.state.sectionAvail ?
					<div>
				 	<Environment list={this.state.constList} open={this.toggelSection}/>
				 	<Art list={this.state.constList} open={this.toggelSection}/> 
				 	<Report list={this.state.constList} open={this.toggelSection}/>
				 	<International list={this.state.constList} open={this.toggelSection}/>
				 	<Defence list={this.state.constList} open={this.toggelSection}/>
				 	<National list={this.state.constList} open={this.toggelSection}/>
					</div>
					: <span></span>}
			</div>
			)
	}
}